<template>
    <table class="w-100">
      <thead>
        <tr>
          <th>RDF Property</th>
          <th>CSV Column</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="pathURL in pathURLs" :key="pathURL">
          <td class="code">{{ pathURL }}</td>
          <td style="width: 30%;">
            <select v-model="localPathColumnMapping[pathURL]" class="w-100" @change="emitUpdate">
                <option value="" disabled selected>Select column</option>
                <option v-for="csvColumn in csvColumns" :key="csvColumn" :value="csvColumn">
                    {{ csvColumn }}
                </option>
            </select>
          </td>
        </tr>
      </tbody>
    </table>
  </template>
  
  <script>
  export default {
    props: {
      shapeURL: String,
      pathURLs: Array,
      csvColumns: Array,
      pathColumnMapping: Object
    },
    data() {
      return {
        localPathColumnMapping: this.pathColumnMapping
      };
    },
    methods: {
      emitUpdate() {
        this.$emit('update-mapping', { shapeURL: this.shapeURL, mapping: this.localPathColumnMapping });
      }
    }
  };
  </script>
  
  <style scoped>

  table, tr, th, td {
      border-collapse: collapse;
      border: 1px solid;
      border-color: #cfcfcf;
  }
  
  table {
      margin: 24px 0px 0px 0px;
      text-align: left;
  }
  
  td, th {
      padding: 4px;
  }
  .shapefield {
      border: 2px solid;
      border-color: #008877;
      margin-bottom: 24px;
      padding: 24px 16px 16px 16px;
      border-style: groove;
      position: relative;
  }
  
  legend {
      position: absolute;
      top: -9px;
      background-color: #fff;
  }  
  </style>