<template>
  <div class="upload-field">
    <h4>Upload SHACL shapes</h4>
    <input type="file" ref="fileInput" class="file-input" @change="processTtl()" />
    Files:
    <ul class="uploaded-files">
      <li v-if="uploadedFiles.length === 0" value="" disabled selected>No Files Uploaded</li>
      <li v-for="(file, index) in uploadedFiles" :key="index">{{ file.name }}</li>
    </ul>
  </div>
</template>

<script>

import { graph, parse, Namespace } from 'rdflib';

export default {
  data() {
    return {
      uploadedFiles: [],
      fileData: {},
    };
  },
  methods: {
    processTtl() {
      const fileInput = this.$refs.fileInput;
      const file = fileInput.files[0];

      if (file) {
        this.uploadedFiles.push(file);
        //console.log("uploadedFiles:", this.uploadedFiles)

        const reader = new FileReader();
        reader.onload = (e) => {
          try {
            // Setup Graph data
            const ttlData = e.target.result;
            const store = graph();
            const baseURI = 'http://example.org';
            parse(ttlData, store, baseURI, 'text/turtle');

            // Setup Namespaces
            var SH = Namespace("http://www.w3.org/ns/shacl#");
            //var DCTERMS = Namespace("http://purl.org/dc/terms/");
            var shNodeShape = SH('NodeShape');
            var shProperty = SH('property');
            var shPath = SH('path');
            //var dcTitle = DCTERMS('title')

            // Get Node Shapes
            const shapes = store.match(null, null, shNodeShape, null);
            shapes.forEach((triple) => {
                const NodeShapes = triple.subject;
                this.fileData[NodeShapes.value] = {}

                // Get Property IDs based on Node Shapes
                const propId = store.match(NodeShapes, shProperty, null, null);
                propId.forEach((triple) => {
                    const property = triple.object;

                    // Get Property Paths based on IDs
                    const paths = store.match(property, shPath, null, null)
                    paths.forEach((triple) => {
                      const path = triple.object

                      // Add paths to NodeShape dict
                      this.fileData[NodeShapes.value][path.value] = "";
                    });
                });
                // Get NodeShape Name based on Shape --> To be integrated in UI for readability
                /*const tnames = store.match(NodeShapes, dcTitle, null, null)
                tnames.forEach((triple) => {
                const name = triple.object
                console.log("Shapename:", name)
              })*/
            });
            this.$emit('file-uploaded', {fileData: this.fileData, files: this.uploadedFiles});
          } catch (error) {
            console.error('Error parsing TTL data:', error);
          }
        };
        reader.readAsText(file);
      }
    }

  }
};
</script>

  
