<template>
  <h3>3. Generate and Display RDF-Graph</h3>
  <button class="btn" @click="generateRDF()">Generate RDF-Graph</button>
  <div class="ttl-output">
    <h3>RDF-Graph</h3>
    <button class="copy-btn" @click="copyToClipboard()">{{ copyButtonText }}</button>
    <pre>{{ rdfGraph }}</pre>
  </div>
</template>

<script>
  //import axios from 'axios';
import { graph, parse, Namespace, namedNode, st, serialize, literal } from 'rdflib';
import Papa from "papaparse";

  export default {
    props: {
      mappingData: Object,
      ttlFiles: Array,
      csvFiles: Array
    },
    data() {
        return {
          rdfGraph: "no graph generated",
          tripleStore: null,
          PathColumnMapping: null,
          ShapeCsvMapping: null,
          copyButtonText: 'Copy graph',
        }
    },
    methods: {
      copyToClipboard() {
        this.copyButtonText = 'âœ“ Copied!';
        console.log("copyButtonText:", this.copyButtonText)
        setTimeout(() => {
          this.copyButtonText = 'Copy graph';
        }, 1000);
        if (navigator.clipboard && window.isSecureContext) {
          return navigator.clipboard.writeText(this.rdfGraph);
        } else {
          let textArea = document.createElement("textarea");
          textArea.value = this.rdfGraph;
          textArea.style.position = "fixed";  // Avoid scrolling to bottom
          document.body.appendChild(textArea);
          textArea.focus();
          textArea.select();
          try {
            document.execCommand('copy');
            textArea.remove();
          } catch (err) {
            console.error('Fallback: Oops, unable to copy', err);
          }
        }        
      },

      generateRDF() {
        this.parseTtlFile();
        this.parseMappings();
        this.createGraph();

      },

      createGraph() {

      const newTripleStore = graph();

      // Setup Namespaces
      var SH = Namespace("http://www.w3.org/ns/shacl#");
      var RDF = Namespace('http://www.w3.org/1999/02/22-rdf-syntax-ns#')

      var shProperty = SH('property');
      var shDatatype = SH('datatype');
      var shNode = SH('node')

      var rdfType = RDF('type');

      //const example = Namespace('https://example.com/');

      
      // include counter for csv-files to ensure correct serialization
      let filesToProcess = Object.keys(this.csvFiles).length;
      let filesProcessed = 0;

      for (let csvFileKey in this.csvFiles) {
        const file = this.csvFiles[csvFileKey];
        const fileName = file.name;
        for (const shape in this.ShapeCsvMapping) {
          if (this.ShapeCsvMapping[shape] === fileName) { // Get matching CSV-File for NodeShape
            const reader = new FileReader();
            reader.onload = (e) => {
              const csvData = e.target.result;
              Papa.parse(csvData, {
                complete: (result) => {
                  const rows = result.data;
                  const headers = rows.shift();
                  rows.forEach(row => {
                    headers.forEach((header, index) => {
                      for (let prop in this.PathColumnMapping[shape]) {
                        if (this.PathColumnMapping[shape][prop] === header) { // Get matching CSV-Header for Shape Property Path

                          // Add Triples for NodeShapes
                          const t1 = this.tripleStore.match(namedNode(shape), shProperty, null, null);
                          t1.forEach(triple => {
                            const s1 = triple.subject;
                            const o1 = triple.object;
                            const newNodeShape = namedNode(shape + row[0]);
                            const new_t1 = st(newNodeShape, rdfType, s1);
                            newTripleStore.add(new_t1);
                            //console.log("object 1:", o1);
                            // Add Triples for Properties
                            const t2 = this.tripleStore.match(o1, null, null, null);
                            t2.forEach(triple => {
                              const o2 = triple.object;
                              if (prop === o2.value) {
                                const t3 = this.tripleStore.match(o1, shDatatype, null, null);
                                t3.forEach(triple => {
                                  const o3 = triple.object
                                  const new_t3 = st(newNodeShape, o2, literal(row[index], o3));
                                  newTripleStore.add(new_t3);
                                });
                                const t4 = this.tripleStore.match(o1, shNode, null, null);
                                t4.forEach(triple => {
                                  const o4 = triple.object
                                  const nodeIds = row[index].split(",");
                                  for (const id of nodeIds) {
                                    const nodeUrl = namedNode(o4.value + id);
                                    const new_t4 = st(newNodeShape, o2, nodeUrl);
                                    newTripleStore.add(new_t4);
                                  }
                                });
                              }
                            });
                          });
                        }
                      }
                    });
                  });

                  filesProcessed++;
                  if (filesProcessed === filesToProcess) {
                    serialize(undefined, newTripleStore, undefined, 'text/turtle', (err, str) => {
                      if (err) {
                        console.log('Serialization error:', err);
                      } else {
                        this.rdfGraph = str;
                      }
                    });
                  }
                }
              });
            };
            reader.readAsText(file);
          }
        }
      }
    },


    parseMappings() {/*
      console.log("MappingData:", this.mappingData);
      console.log("PathColumnMapping", this.mappingData["PathColumnMapping"]);
      console.log("ShapeCsvMapping", this.mappingData["ShapeCsvMapping"]);*/

      this.PathColumnMapping = this.mappingData["PathColumnMapping"]
      this.ShapeCsvMapping = this.mappingData["ShapeCsvMapping"]


    },

      async parseTtlFile() {
        try {
          // Extract the File object from the Proxy
          const ttlFile = this.ttlFiles[0];
          let ttlData = null;
          const store = graph();
          const baseURI = 'http://example.org';

          // If ttlData is a file object, read it as text
          if (ttlFile instanceof File) {
            ttlData = await this.readFileAsText(ttlFile);
            //console.log("ttlData after reading:", ttlData); // Debug: Check the read content
          }

          // Parse the TTL data
          parse(ttlData, store, baseURI, 'text/turtle');
          this.tripleStore = store; // TEMP

        } catch (error) {
          console.error('Error parsing TTL data:', error);
        }
      },

    readFileAsText(file) {
      return new Promise((resolve, reject) => {
        const reader = new FileReader();
        reader.onload = () => resolve(reader.result);
        reader.onerror = () => reject(reader.error);
        reader.readAsText(file);
      });
    }
  }
}
</script>

<style scoped>
.ttl-output {
    background-color: #0e0e0e;
    color: #b4b4b4;
    padding: 10px;
    border-radius: 5px;
    font-size: 0.7em;
    margin: 24px 0px 24px 0px;

    position: relative;
}
.copy-btn {
    display: flex;
    flex-direction: column;
    white-space: nowrap;
    background-color: #434343;
    gap: 10px;
    padding: 12px;
    text-decoration: none;
    color: #a2a2a2;
    border-radius: 6px;
    border: 0;
    outline: none;
    font-size: 1rem;
    transition: all 0.2s ease-out;

    position: absolute;
    top: 10px;   /* Adjust these values as needed */
    right: 10px;
    cursor: pointer;
  }

  .copy-btn:hover {
      cursor: pointer;
      background-color: #5f5f5f;
      color: #FFF;
  }
</style>
