<template>
    <h3>2. Match Shape properties with CSV-Columns</h3>
    <div v-for="shapeURL in shapeURLs" :key="shapeURL" class="shapefield">
      <legend>{{ shapeURL }}</legend>
      <div>
        Choose CSV: 
        <select @change="handleCsvSelection($event, shapeURL)">
          <option v-for="fileName in csvFileNames" :key="fileName" :value="fileName">
            {{ fileName }}
          </option>
        </select>
      </div>
      <div v-if="PathColumnMapping[shapeURL]">
        <ShapeTable 
          :shapeURL="shapeURL"
          :pathURLs="pathURLs(shapeURL)"
          :csvColumns= "csvColumns(shapeURL)"
          :pathColumnMapping="PathColumnMapping[shapeURL]"
          @update-mapping="updatePathColumnMapping"
          />
      </div>
    </div>
</template>
<!--

-->
  
<script>
import ShapeTable from './ShapeTable.vue';

export default {
  components: { ShapeTable },
  props: {
    ttlInput: Object, // Shape properties
    csvInput: Object // CSV files
  },
  emits: ['pass-ttl'],
  data() {
    return {
      ShapeColumnList: {},
      PathColumnMapping: {},
      ShapeCsvMapping: {},
    };
  },
  computed: {
    shapeURLs() {
      return Object.keys(this.ttlInput);
    },
    csvFileNames() {
      return Object.keys(this.csvInput);
    }
  },
  watch: {
  ttlInput: {
    handler() {
        this.PathColumnMapping = {};
        this.setDefaultCsvFiles();
        this.initializePathMappings();
      }
    },
    csvInput: {
    handler() {
        this.setDefaultCsvFiles();
      }
    },
  },
  methods: {

    pathURLs(shapeURL) {
      return Object.keys(this.ttlInput[shapeURL]);
    },

    csvColumns(shapeURL) {
      return Object.values(this.ShapeColumnList[shapeURL]);
    },

    initializePathMappings() {
      this.PathColumnMapping = this.ttlInput;
    },

    setDefaultCsvFiles() {
      const firstCsvFile = this.csvFileNames[0];
      if (firstCsvFile) {
        Object.keys(this.ttlInput).forEach(shapeURL => {
          this.handleCsvSelection({ target: { value: firstCsvFile } }, shapeURL);
        });
      } else {
        console.log("Error: No CSV-File available to set as default")
      }
    },
    handleCsvSelection(event, shapeURL) {
      const selectedFileName = event.target.value;
      this.ShapeCsvMapping[shapeURL] = selectedFileName;
      this.ShapeColumnList[shapeURL] = this.csvInput[selectedFileName];
      this.$emit('pass-ttl', {
        PathColumnMapping: this.PathColumnMapping,
        ShapeCsvMapping: this.ShapeCsvMapping
      });
    },
    updatePathColumnMapping({ shapeURL, mapping }) {
      this.PathColumnMapping = {
        ...this.PathColumnMapping,
        [shapeURL]: mapping
      };
    }
  },
  mounted() {
  this.setDefaultCsvFiles();
  this.initializePathMappings();
}
};
</script>


  
