<template>
    <div class="content">
        <h3>1. Upload SHACL shapes and CSV Files</h3>
        <div class="upload-container">
            <upload-ttl-form @file-uploaded="handleTtlUploaded"></upload-ttl-form>
            <upload-csv-form @file-uploaded="handleCsvUploaded"></upload-csv-form>
        </div>
      <form-generator 
        :ttlInput="ttlInput" 
        :csvInput="unwrappedCsvInput"
        @pass-ttl="handleTtlGeneration"
      ></form-generator>
      <ttl-output 
      :mappingData="mappingData"
      :ttlFiles="ttlFiles"
      :csvFiles="csvFiles">
    </ttl-output>
    </div>
  </template>
  
  <script>
  import FormGenerator from '@/components/FormGenerator.vue';
  import UploadTtlComponent from '@/components/UploadTtlComponent.vue';
  import UploadCsvComponent from '@/components/UploadCsvComponent.vue';
  import OutputComponent from '@/components/OutputComponent.vue';

  
  export default {
    components: {
      'form-generator': FormGenerator,
      'upload-ttl-form': UploadTtlComponent,
      'upload-csv-form': UploadCsvComponent,
      'ttl-output': OutputComponent
    },
    data() {
      return {
        ttlInput: {"http://example.shape/": {"http://example.property/1": "example_property 1", "http://example.property/2": "example_property 2"}},
        ttlFiles: null,
        csvInput: {"Example.csv": ["Example column 1", "Example column 2"]},
        csvFiles: null,
        mappingData: {},
      };
    },
    methods: {
      handleTtlUploaded(ttlDump) {
        const { fileData, files } = ttlDump;
        this.ttlInput = fileData;
        this.ttlFiles = files;
      },
      handleCsvUploaded(csvDump) {
        const { fileData, files } = csvDump;
        this.csvInput = fileData;
        this.csvFiles = files;
      },
      handleTtlGeneration(mappingData) {
        this.mappingData = mappingData;
      }
    },
    computed: {
    unwrappedCsvInput() {
      return { ...this.csvInput };
    }
},
  };
  </script>
  
  
  <style>  

