<template>
  <div class="upload-field">
    <h4>Upload CSV</h4>
    <input type="file" ref="fileInput" class="file-input" multiple @change="processCsv()"/>
    Files:
    <ul class="uploaded-files">
      <li v-if="uploadedFiles.length === 0" value="" disabled selected>No Files Uploaded</li>
      <li v-for="(file, index) in uploadedFiles" :key="index">{{ file.name }}</li>
    </ul>
  </div>
</template>

<script>

export default {
  data() {
    return {
      uploadedFiles: [],
      fileData: {}
    };
  },
  methods: {
    processCsv() {
      const fileInput = this.$refs.fileInput;
      for (let i = 0; i < fileInput.files.length; i++) {
        const file = fileInput.files[i];
        this.uploadedFiles.push(file);

        const reader = new FileReader();
        reader.onload = (e) => {
          const csvData = e.target.result;
          const lines = csvData.split("\n");
          const heads = lines[0].split(",");

          for (let i = 0; i < heads.length; i++) {
            heads[i] = heads[i].trim();
          }

          this.fileData[file.name] = heads;
        };
        reader.readAsText(file);
        this.$emit('file-uploaded', {fileData: this.fileData, files: this.uploadedFiles});
        //console.log(this.fileData)
      }
    }
  }
}


</script>

